import axios from "axios";
import styles from "./All.module.scss";

export default {
    data() {
        return {
            loading: true,
            cats: []
        };
    },
    mounted: async function() {
        const res = await axios.get("/api/cats");
        this.loading = false;
        this.cats = res.data;
    },
    render() {
        if (this.loading)
            return (
                <div>
                    <div>Loading all the cats. . .</div>
                </div>
            );
        return (
            <div class={styles.grid}>
                {this.cats.map(cat => (
                    <div
                        class="border shadow rounded d-flex flex-column"
                        style={{ overflow: "hidden" }}
                    >
                        <img class={styles.catImage} src={cat.image} alt="" />
                        <div class="p-3">
                            <span class="font-weight-bold">Description:</span>{" "}
                            {cat.description}
                        </div>
                    </div>
                ))}
            </div>
        );
    }
};
