import axios from "axios";

export default {
    data() {
        return {
            loading: true,
            cat: {},
            stars: 0,
            isInvalid: false,
            average: null,
            disableButton: false,
            nextButton: false
        };
    },
    mounted() {
        this.getCat();
    },
    methods: {
        getCat: async function() {
            this.loading = true;
            this.stars = 0;
            this.disableButton = false;
            const res = await axios.get("/api/cats/random");
            this.cat = res.data.cat[0];
            this.nextButton = false;
            this.average = res.data.average;
            this.loading = false;
        },
        submit: async function() {
            if (this.rating === 0) this.isInvalid = true;
            else {
                this.disableButton = true;
                this.isInvalid = false;
                const data = {
                    stars: this.stars,
                    cat_id: this.cat.id
                };
                await axios.post("/api/ratings", data);
                this.nextButton = true;
            }
        }
    },
    render() {
        if (this.loading)
            return (
                <div>
                    <h4>Loading your cat. . .</h4>
                </div>
            );
        return (
            <div>
                <h1 class="text-center mb-4">Rate That Cat!</h1>
                <div class="text-center d-sm-flex align-items-center justify-content-center mb-sm-3">
                    <h5 class="mb-3 mb-sm-0 mr-sm-3">
                        Please choose your rating:
                    </h5>
                    <b-form-select
                        class={`mb-3 mb-sm-0 ${this.isInvalid && "is-invalid"}`}
                        vModel={this.stars}
                        style={{ maxWidth: "200px" }}
                    >
                        <option disabled value={0}>
                            select one
                        </option>
                        <option value={1}>⭐</option>
                        <option value={2}>⭐⭐</option>
                        <option value={3}>⭐⭐⭐</option>
                        <option value={4}>⭐⭐⭐⭐</option>
                        <option value={5}>⭐⭐⭐⭐⭐</option>
                    </b-form-select>
                </div>
                <div class="text-center mb-5">
                    {this.nextButton ? (
                        <b-button onClick={this.getCat} variant="success">
                            Next Cat
                        </b-button>
                    ) : (
                        <b-button
                            disabled={this.disableButton}
                            onClick={this.submit}
                            variant="primary"
                        >
                            Submit
                        </b-button>
                    )}
                </div>
                <p class="text-center mb-0">
                    <span class="font-weight-bold">Average Stars: </span>
                    {this.average || "No ratings yet!"}
                </p>
                <p class="text-center mb-2">
                    <span class="font-weight-bold">Description: </span>
                    {this.cat.description}
                </p>
                <div class="d-flex mx-auto" style={{ maxWidth: "500px" }}>
                    <img
                        class="w-100 border rounded shadow"
                        src={this.cat.image}
                        alt=""
                    />
                </div>
            </div>
        );
    }
};
